<template>
  <div>
    <!-- 修改客户表单 -->
    <Modal  title="修改客户信息" @on-ok="modifyCustomer"
    @on-cancel="onCancel"
    :on-open-modify-modal="modal=true" :customer="customer" v-model="modal">
      <Form ref="" :model="customer" :rules="customerValidate" :label-width="120">
        <FormItem label="姓名" prop="realname" >
          <Input v-model="customer.realname" style="width: 220px;"></Input>
        </FormItem>
        <FormItem label="联系电话" prop="phone">
          <Input v-model="customer.phone" style="width: 220px;"></Input>
        </FormItem>
        <FormItem label="备注" prop="remark">
          <Input v-model="customer.remark" style="width: 220px;"></Input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
<script>
export default {
  name: 'cutomermodify',
  props: ['customer'],
  data () {
    return {
      modal: false,
      // customer: {
      //   realname: '',
      //   phone: '',
      //   remark: '',
      //   ownerId: 2
      // },
      customerValidate: {
        realname: [
          {
            required: true,
            message: '姓名不能为空'
          }
        ],
        phone: [
          {
            required: true,
            message: '联系电话不能为空'
          }
        ]
      }
    }
  },
  methods: {
    onCancel () {
      this.$emit('on-close-cancel-modify', false)
    },
    modifyCustomer () {
      console.log('修改客户')
      console.log(this.cusotmer)
      this.postRequest('/api/user/update/' + this.customer.id, this.customer).then(resp => {
        console.log(resp)
        if (resp.status === 200) {
          this.$Message.info('客户修改成功')
          this.$emit('on-close-cancel-modify', false)
          this.$emit('on-handle-modify-result', true)
        }
      })
    }
  }

}
</script>



