<template>
  <div class="truck_list">
    <div class="operate">
      车牌号：
      <Input v-model="searchParam.licensePlate" @on-change="search" style="width: 150px; margin-right: 10px" />
      <Button type="primary" icon="md-search" >搜索</Button>
      <Button type="success" icon="md-add" class="btn_add" @click="showTruckAddModal">添加车辆</Button>
    </div>
    <Table border stripe width="600" :columns="truckColumns" :data="truckList" style="border-radius: 3px;">
      <template slot-scope="{ row, index }" slot="action">
        <Button type="primary" size="small" style="margin-right: 5px" @click="show(index)">编辑</Button>
        <Button type="error" size="small" @click="remove(row, index)">删除</Button>
      </template>
    </Table>
    <div v-if="addModalVisiable">
      <TruckAddModal @on-close-cancel-add="onCloseCancelAdd" @on-handle-add-result="onHandleAddResult"></TruckAddModal>
    </div>
  </div>
</template>
<script>
import TruckAddModal from './TruckAddModal'

export default {
  name: 'trucklist',
  components: {
    TruckAddModal
  },
  data () {
    return {
      searchParam: {
        userId: 2,
        licensePlate: ''
      },
      userId: 2,
      addModalVisiable: false,
      truck: {
        licensePlate: '',
        maxCapacity: null,
        userId: 2
      },
      truckList: [],
      truckColumns: [
        {
          title: '车辆编号',
          key: 'id',
          width: '100px',
          align: 'center'
        },
        {
          title: '车牌号',
          key: 'licensePlate',
          align: 'center'
        },
        {
          title: '最大装载容量(吨)',
          key: 'maxCapacity',
          align: 'center'
        },
        {
          title: '操作',
          slot: 'action',
          width: '200px',
          align: 'center'
        }
      ]
    }
  },
  created () {
    this.getTruck()
  },
  methods: {
    search () {
      this.postRequest('/api/truck/search', this.searchParam).then(resp => {
        if (resp.status === 200) {
          this.truckList = resp.data
        }
      })
    },
    getTruck () {
      let _this = this
      this.getRequest('/api/truck/user/' + this.userId).then(resp => {
        console.log(resp.data)
        _this.truckList = resp.data
      })
    },
    show (index) {
      this.$Modal.info({
        title: '详情',
        content: '内容'
      })
    },
    remove (row, index) {
      this.deleteRequest('/api/truck/delete/' + row.id).then(resp => {
        console.log(resp)
        if (resp.status === 200) {
          this.truckList.splice(index, 1)
        }
      })
    },
    showTruckAddModal () {
      console.log('你点击了添加车辆按钮')
      this.addModalVisiable = true
      console.log(this.addModalVisiable)
      this.$emit('on-open-add-modal')
    },
    onCloseCancelAdd (bool) {
      this.addModalVisiable = bool
    },
    onHandleAddResult (bool) {
      if (bool === true) {
        this.getTruck()
      }
    }
  }
}
</script>
<style scoped>
.truck_list {
  width: 600px;
}
.operate {
  margin: 20px 0;
}
.btn_add {
  float: right;
}
</style>



