<template>
  <div class="truck_list">
    <div class="operate">
      <!-- 车牌号：
      <Input @on-change="search" style="width: 150px; margin-right: 10px" />
      <Button type="primary" icon="md-search" >搜索</Button> -->
      <Button type="success" icon="md-add" class="btn_add" @click="showTruckAddModal">添加车辆</Button>
    </div>
    <Table border stripe :columns="cementColumns" :data="cementList" style="border-radius: 3px;">
      <template slot-scope="{ row, index }" slot="action">
        <Button type="primary" size="small" style="margin-right: 5px" @click="show(index)">编辑</Button>
        <Button type="error" size="small" @click="remove(row, index)">删除</Button>
      </template>
    </Table>
  </div>
</template>
<script>

export default {
  name: 'cementlist',
  components: {
  },
  data () {
    return {
      userId: 2,
      addModalVisiable: false,
      cement: {
        name: ''
      },
      cementList: [],
      cementColumns: [
        {
          title: '水泥编号',
          key: 'id',
          width: '100px',
          align: 'center'
        },
        {
          title: '水泥名称',
          key: 'name',
          align: 'center'
        },
        {
          title: '操作',
          slot: 'action',
          width: '200px',
          align: 'center'
        }
      ]
    }
  },
  created () {
    this.getCement()
  },
  methods: {
    getCement () {
      let _this = this
      this.getRequest('/api/cement/').then(resp => {
        console.log(resp.data)
        _this.cementList = resp.data
      })
    },
    show (index) {
      this.$Modal.info({
        title: '详情',
        content: '内容'
      })
    },
    remove (row, index) {
      this.deleteRequest('/api/cement/delete/' + row.id).then(resp => {
        console.log(resp)
        if (resp.status === 200) {
          this.truckList.splice(index, 1)
        }
      })
    },
    showTruckAddModal () {
      console.log('你点击了添加车辆按钮')
      this.addModalVisiable = true
      console.log(this.addModalVisiable)
      this.$emit('on-open-add-modal')
    },
    onCloseCancelAdd (bool) {
      this.addModalVisiable = bool
    },
    onHandleAddResult (bool) {
      if (bool === true) {
        this.getTruck()
      }
    }
  }
}
</script>
<style scoped>
.truck_list {
  width: 600px;
}
.operate {
  margin: 20px 0;
}
.btn_add {
  float: right;
}
</style>



