<template>
  <div>
    <div class="operate">
      完成情况：
      <Select v-model="searchParam.scope" style="width:70px; margin-right: 10px" @on-change="search">
        <Option v-for="item in optionList" :value="item.value" :key="item.value" >{{ item.label }}</Option>
      </Select>
      客户姓名：
      <Input v-model="searchParam.realname"  @on-change="search" style="width: 100px; margin-right: 10px" />
      地址：
      <Input v-model="searchParam.addressName" @on-change="search" style="width: 180px; margin-right: 10px" />
      起始日期：
      <DatePicker type="date" placement="bottom-end" v-model="searchParam.fromDate" placeholder="from" style="width: 120px; margin-right: 10px"></DatePicker>
      终止日期：
      <DatePicker type="date" placement="bottom-end" v-model="searchParam.toDate" placeholder="to" style="width: 120px; margin-right: 10px"></DatePicker>
      <Button type="primary" icon="md-search" @click="search">搜索</Button>
      <Button type="success" icon="md-add" class="btn_add" @click="showOrderAddModal">添加订单</Button>
    </div>
    <Table border stripe :columns="orderColumns" :data="orderList">
    <template slot-scope="{ row, index }" slot="action">
      <Button type="primary" size="small" style="margin-right: 5px" @click="show(index)">编辑</Button>
      <Button type="error" size="small" @click="remove(row, index)">删除</Button>
    </template>
    </Table>
    <div v-if="addModalVisiable" >
      <OrderAddModal @on-close-cancel-add="onCloseCancelAdd" @on-handle-add-result="onHandleAddResult"></OrderAddModal>
    </div>
  </div>
</template>
<script>
// import { resolve } from 'url';
import OrderAddModal from './OrderAddModal'
export default {
  name: 'orderlist',
  components: {
    OrderAddModal
  },
  data () {
    return {
      searchParam: {
        userId: 2,
        scope: 0,
        realname: '',
        addressName: '',
        fromDate: '2019-01-01',
        toDate: new Date()
      },
      addModalVisiable: false,
      userId: 2,
      order: {
        customerId: null,
        addressId: null,
        cementId: null,
        unitPrice: 400,
        num: 10,
        userId: 2
      },
      orderList: [],
      choice: [],
      optionList: [
        {
          value: 0,
          label: '全部'
        },
        {
          value: 1,
          label: '已完成'
        },
        {
          value: 2,
          label: '未完成'
        }
      ],
      orderColumns: [
        {
          title: '订单编号',
          key: 'id',
          width: '100px',
          align: 'center'
        },
        {
          title: '客户姓名',
          key: 'customerName',
          width: '120px',
          align: 'center'
        },
        {
          title: '收货地址',
          key: 'addressName',
          width: '309px',
          align: 'center'
        },
        {
          title: '水泥名称',
          key: 'cementName',
          width: '100px',
          align: 'center'
        },
        {
          title: '单价',
          key: 'unitPrice',
          width: '80px',
          align: 'center'
        },
        {
          title: '数量',
          key: 'num',
          width: '80px',
          align: 'center'
        },
        {
          title: '创建时间',
          key: 'createdTime',
          width: '150px',
          align: 'center'
        },
        {
          title: '状态',
          key: 'deliveryId',
          align: 'center',
          width: '80px',
          render: (h, params) => {
            return h('span', {
              style: {
                color: (params.row.deliveryId > 0) ? 'green' : 'red'
              }
            }, (params.row.deliveryId > 0) ? '已完成' : '未完成')
          }
        },
        {
          title: '操作',
          slot: 'action',
          width: '200px',
          align: 'center'
        }
      ]
    }
  },
  created () {
    this.getOrders()
  },
  methods: {
    getOrders: function () {
      // let _this = this
      // this.$ajax({
      //   method: 'get',
      //   url: 'http://localhost:8080/api/orders/all'
      //   // headers: {'Access-Control-Allow-Origin': '*'}
      // }).then((response) => {
      //   console.log(response)
      //   _this.orderList = response.data
      // }).catch(function (error) {
      //   console.log(error)
      // })
      // this.getRequest('/api/admin/test').then(resp => {
      //   console.log(resp.data)
      // })
      let _this = this
      this.getRequest('/api/orders/all/' + this.userId).then(resp => {
        console.log(resp.data)
        _this.orderList = resp.data
        console.log(_this.orderList)
      })
    },
    showOrderAddModal () {
      console.log('你点击了添加订单按钮')
      this.addModalVisiable = true
      this.$emit('on-open-add-modal')
    },
    onCloseCancelAdd (bool) {
      this.addModalVisiable = bool
    },
    onHandleAddResult (bool) {
      if (bool === true) {
        this.getOrders()
      }
    },
    show (index) {
      this.$Modal.info({
        title: '订单详情',
        content: '订单内容'
      })
    },
    remove (row, index) {
      this.deleteRequest('/api/orders/delete/' + row.id).then(resp => {
        console.log(resp)
      })
      this.orderList.splice(index, 1)
    },
    search () {
      this.postRequest('/api/orders/search', this.searchParam).then(resp => {
        if (resp.status === 200) {
          this.orderList = resp.data
        }
      })
    }
  }
}
</script>
<style scoped>
.operate {
  margin: 20px 0;
}
.btn_add {
  float: right;
}
</style>



