<template>
  <div class="employee_list">
    <div class="operate">
      职位：
      <Select v-model="searchParam.jobId" @on-change="search" style="width:100px; margin-right: 10px" >
        <Option :value="0" :key="0">全部</Option>
        <Option v-for="item in jobList" :value="item.id" :key="item.id" >{{ item.name }}</Option>
      </Select>
      姓名：
      <Input v-model="searchParam.realname"  @on-change="search" style="width: 100px; margin-right: 10px" />
      联系电话：
      <Input v-model="searchParam.phone"  @on-change="search" style="width: 100px; margin-right: 10px" />
      <Button type="primary" icon="md-search" @click="search">搜索</Button>
      <Button type="success" icon="md-add" class="btn_add" @click="showEmployeeAddModal">添加员工</Button>
    </div>
    <Table border :columns="employeeColumns" :data="employeeList" style="border-radius: 3px;">
      <template slot-scope="{ row, index }" slot="action">
        <Button type="primary" size="small" style="margin-right: 5px" @click="show(row, index)">编辑</Button>
        <Button type="error" size="small" @click="remove(row, index)">删除</Button>
      </template>
    </Table>
    <div v-if="addModalVisiable">
      <EmployeeAddModal @on-close-cancel-add="onCloseCancelAdd" @on-handle-add-result="onHandleAddResult"></EmployeeAddModal>
    </div>
  </div>
</template>
<script>
import EmployeeAddModal from './EmployeeAddModal'
export default {
  name: 'employeelist',
  components: {
    EmployeeAddModal
  },
  data () {
    return {
      searchParam: {
        ownerId: 2,
        jobId: 0,
        realname: '',
        phone: ''
      },
      userId: 2,
      addModalVisiable: false,
      employee: {
        realname: '',
        phone: '',
        jobId: null,
        ownerId: 2
      },
      jobList: [],
      employeeList: [],
      employeeColumns: [
        {
          title: '员工编号',
          key: 'id',
          width: '100px',
          align: 'center'
        },
        {
          title: '姓名',
          key: 'realname',
          width: '100px',
          align: 'center'
        },
        {
          title: '联系电话',
          key: 'phone',
          width: '150px',
          align: 'center'
        },
        {
          title: '职位名称',
          key: 'jobName',
          width: '100px',
          align: 'center'
        },
        {
          title: '备注',
          key: 'remark',
          align: 'center'
        },
        {
          title: '操作',
          slot: 'action',
          width: '200px',
          align: 'center'
        }
      ]
    }
  },
  created () {
    this.getAllEmployee()
    this.getAllJob()
  },
  methods: {
    search () {
      console.log(this.searchParam)
      this.postRequest('/api/user/employee/search', this.searchParam).then(resp => {
        if (resp.status === 200) {
          this.employeeList = resp.data
        }
      })
    },
    getAllJob () {
      let _this = this
      this.getRequest('/api/job/').then(resp => {
        _this.jobList = resp.data
        console.log(resp)
      })
    },
    getAllEmployee () {
      let _this = this
      this.getRequest('/api/user/employee/' + this.userId).then(resp => {
        console.log(resp.data)
        _this.employeeList = resp.data
      })
    },
    showEmployeeAddModal () {
      console.log('你点击了添加员工按钮')
      this.addModalVisiable = true
      this.$emit('on-open-add-modal')
    },
    show (row, index) {
      this.$Modal.info({
        title: '员工详情',
        content: '员工内容'
      })
    },
    remove (row, index) {
      this.deleteRequest('/api/user/delete/' + row.id).then(resp => {
        console.log(resp)
      })
      this.employeeList.splice(index, 1)
    },
    onCloseCancelAdd (bool) {
      this.addModalVisiable = bool
    },
    onHandleAddResult (bool) {
      if (bool === true) {
        this.getAllEmployee()
      }
    }
  }
}
</script>
<style scoped>
.employee_list {
  width: 900px;
}
.operate {
  margin: 20px 0;
}
.btn_add {
  float: right;
}
</style>



