<template>
  <div>
    <!-- 添加订单表单 -->
    <Modal v-model="modal" title="添加订单"
    @on-ok="addOrder"
    @on-cancel="onCancel"
    :on-open-add-modal="modal=true">
      <Form ref="" :model="order" :rules="orderValidate" :label-width="120">
        <FormItem label="客户姓名" prop="customerId" >
          <Select v-model="order.customerId" style="width:100px" @on-change="getAddressByCustomerId" filterable>
            <Option v-for="item in customerList" :value="item.id" :key="item.id">{{ item.realname }}</Option>
          </Select>
        </FormItem>
        <FormItem label="收货地址" prop="addressId">
          <Select v-model="order.addressId" style="width:100px" filterable>
            <Option v-for="item in addressList" :value="item.id" :key="item.id">{{ item.addressName }}</Option>
          </Select>
        </FormItem>
        <FormItem label="水泥名称" prop="cementId">
          <Select v-model="order.cementId" style="width:100px">
            <Option v-for="item in cementList" :value="item.id" :key="item.id">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="单价" prop="unitPrice">
          <InputNumber :max="600" :min="0" :step="5" v-model="order.unitPrice" style="width:100px"></InputNumber>
        </FormItem>
        <FormItem label="数量" prop="num">
          <InputNumber :max="100" :min="0" v-model="order.num" style="width:100px"></InputNumber>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
<script>
export default {
  name: 'orderaddmodal',
  data () {
    return {
      modal: '',
      addModalVisiable: false,
      userId: 2,
      order: {
        customerId: null,
        addressId: null,
        cementId: null,
        unitPrice: 400,
        num: 10,
        userId: 2
      },
      customerList: [],
      addressList: [],
      cementList: [],
      orderValidate: {
        customerId: [
          {
            required: true,
            message: '客户姓名不能为空'
          }
        ],
        addressId: [
          {
            required: true,
            message: '收货地址不能为空'
          }
        ],
        cementId: [
          {
            required: true,
            message: '水泥名称不能为空'
          }
        ],
        unitPrice: [
          {
            required: true,
            message: '单价不能为空'
          }
        ],
        num: [
          {
            required: true,
            message: '数量不能为空'
          }
        ]
      }
    }
  },
  created () {
    this.getCustomerByUserId()
    this.getAllCement()
  },
  methods: {
    getCustomerByUserId () {
      let _this = this
      this.getRequest('/api/user/customer/' + this.userId).then(resp => {
        console.log(resp)
        _this.customerList = resp.data
        console.log(_this.customerList)
      })
    },
    getAddressByCustomerId () {
      console.log(this.order.customerId)
      let _this = this
      this.getRequest('/api/address/' + this.order.customerId).then(resp => {
        console.log(resp)
        _this.addressList = resp.data.data
        console.log(_this.addressList)
      })
    },
    getAllCement () {
      let _this = this
      this.getRequest('/api/cement/').then(resp => {
        console.log(resp)
        _this.cementList = resp.data.data
      })
    },
    addOrder () {
      // 创建订单
      this.postRequest('/api/orders/add', this.order).then(resp => {
        console.log(resp)
        if (resp.status === 200) {
          this.$Message.info('订单创建成功')
          this.$emit('on-handle-add-result', true)
        }
      })
    },
    onCancel () {
      this.$emit('on-close-cancel-add', false)
    }
  }
}
</script>



