<template>
  <div>
    <!-- 添加员工表单 -->
    <Modal v-model="modal" title="添加员工"
    @on-ok="addEmployee"
    @on-cancel="onCancel"
    :on-open-add-modal="modal=true">
      <Form ref="" :model="employee" :rules="employeeValidate" :label-width="120">
        <FormItem label="姓名" prop="realname" >
          <Input v-model="employee.realname" placeholder="请输入姓名" style="width: 220px;"></Input>
        </FormItem>
        <FormItem label="联系电话" prop="phone">
          <Input v-model="employee.phone" placeholder="请输入联系电话" style="width: 220px;"></Input>
        </FormItem>
        <FormItem label="职位" prop="jobId">
          <Select v-model="employee.jobId" style="width:100px">
            <Option v-for="item in jobList" :value="item.id" :key="item.id">{{ item.name }}</Option>
          </Select>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
<script>
export default {
  data () {
    return {
      modal: false,
      userId: 2,
      employee: {
        realname: '',
        phone: '',
        jobId: null,
        ownerId: 2
      },
      jobList: [],
      employeeValidate: {
        realname: [
          {
            required: true,
            message: '姓名不能为空'
          }
        ],
        phone: [
          {
            required: true,
            message: '联系电话不能为空'
          }
        ],
        jobId: [
          {
            required: true,
            message: '职位不能为空'
          }
        ]
      }
    }
  },
  created () {
    this.getAllJob()
  },
  methods: {
    getAllJob () {
      let _this = this
      this.getRequest('/api/job/').then(resp => {
        _this.jobList = resp.data
        console.log(resp)
      })
    },
    addEmployee () {
      this.postRequest('/api/user/addEmployee', this.employee).then(resp => {
        console.log(resp)
        if (resp.status === 200) {
          this.$Message.info('添加成功')
          this.$emit('on-handle-add-result', true)
        }
        this.$emit('on-close-cancel-add', false)
      })
    },
    onCancel () {
      this.$emit('on-close-cancel-add', false)
    }
  }
}
</script>



