<style scoped>
.layout {
  border: 1px solid #d7dde4;
  background: #f5f7f9;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}
.layout-logo {
  width: 100px;
  height: 30px;
  background: #5b6270;
  border-radius: 3px;
  float: left;
  position: relative;
  top: 15px;
  left: 20px;
}
.layout-nav {
  width: 420px;
  margin: 0 auto;
  margin-right: 20px;
}
</style>
<template>
  <div class="layout">
    <Layout>
      <Header>
        发货管理系统
        <div class="mine">
          <Dropdown>
            <a href="javascript:void(0)">
                <Avatar style="background-color: #87d068" icon="ios-person"/>
                <Icon type="ios-arrow-down"></Icon>
            </a>
            <DropdownMenu slot="list">
                <DropdownItem>个人中心</DropdownItem>
                <DropdownItem @click.native="logout">注销</DropdownItem>
            </DropdownMenu>
          </Dropdown>
        </div>
      </Header>
      <Layout>
        <Sider hide-trigger :style="{background: '#fff'}">
          <Menu active-name="1-2" theme="light" width="auto" :open-names="['客户管理']">
            <MenuItem name="1" to="/home/customerlist">
                <Icon type="ios-people" />
                客户管理
            </MenuItem>
            <MenuItem name="2" to="/home/employeelist">
                <Icon type="ios-people" />
                员工管理
            </MenuItem>
            <MenuItem name="3" to="/home/trucklist">
                <Icon type="ios-car" />
                车辆管理
            </MenuItem>
            <MenuItem name="4" to="/home/orderlist">
                <Icon type="ios-reorder" />
                订单管理
            </MenuItem>
            <Submenu name="5">
              <template slot="title">
                <Icon type="ios-document"></Icon>发货记录管理
              </template>
              <MenuItem name="5-1" to="/home/deliverylist">发货记录列表</MenuItem>
              <MenuItem name="5-2" to="/home/deliverystatisticalanalysis">发货记录统计</MenuItem>
            </Submenu>
            <MenuItem name="6" to="/home/cementlist">
                <Icon type="ios-reorder" />
                水泥管理
            </MenuItem>
            <MenuItem name="6" to="/home/userlist">
                <Icon type="ios-reorder" />
                用户管理
            </MenuItem>
          </Menu>
        </Sider>
        <Layout :style="{padding: '0 24px 24px'}">
          <Breadcrumb :style="{margin: '24px 0'}">
            <BreadcrumbItem to="/home">主页</BreadcrumbItem>
            <BreadcrumbItem v-text="this.$router.currentRoute.name"></BreadcrumbItem>
          </Breadcrumb>
          <Content :style="{padding: '24px', minHeight: '550px', background: '#fff'}">
            <router-view></router-view>
          </Content>
        </Layout>
      </Layout>
    </Layout>
  </div>
</template>
<script>
export default {
  methods: {
    logout: function () {
      this.$Message.info('你点击了注销')
    }
  }
}
</script>
<style scoped>
.mine {
  float: right;
}
</style>



