<template>
  <div class="delivery_list">
    <div class="operate">
      车牌号：
      <Input v-model="searchParam.licensePlate"  @on-change="search" style="width: 100px; margin-right: 10px" />
      司机姓名：
      <Input v-model="searchParam.realname" @on-change="search" style="width: 100px; margin-right: 10px" />
      起始日期：
      <DatePicker type="date" placement="bottom-end" v-model="searchParam.fromDate" placeholder="from" style="width: 120px; margin-right: 10px"></DatePicker>
      终止日期：
      <DatePicker type="date" placement="bottom-end" v-model="searchParam.toDate" placeholder="to" style="width: 120px; margin-right: 10px"></DatePicker>
      <Button type="primary" icon="md-search" @click="search">搜索</Button>
      <Button type="success" icon="md-add" class="btn_add" @click="showDeliveryAddModal">添加发货记录</Button>
    </div>
    <Table border stripe :columns="deliveryColumns" :data="deliveryList" style="border-radius: 3px;">
      <template slot-scope="{ row, index }" slot="action">
        <Button type="primary" size="small" style="margin-right: 5px" @click="show(index)">编辑</Button>
        <Button type="error" size="small" @click="remove(row, index)">删除</Button>
      </template>
    </Table>
    <div v-if="addModalVisiable">
      <DeliveryAddModal @on-close-cancel-add="onCloseCancelAdd" @on-handle-add-result="onHandleAddResult"></DeliveryAddModal>
    </div>
  </div>
</template>
<script>
import DeliveryAddModal from './DeliveryAddModal'
export default {
  name: 'deliverylist',
  components: {
    DeliveryAddModal
  },
  data () {
    return {
      searchParam: {
        userId: 2,
        licensePlate: '',
        realname: '',
        fromDate: '2019-01-01',
        toDate: new Date()
      },
      userId: 2,
      delivery: {
        truckId: null,
        driverId: null,
        deliveryTime: null,
        userId: 2,
        orderIds: [],
        loaderIds: []
      },
      addModalVisiable: false,
      deliveryList: [],
      deliveryColumns: [
        {
          title: '发货记录编号',
          key: 'id',
          width: '120px',
          align: 'center'
        },
        {
          title: '车牌号',
          key: 'licensePlate',
          width: '100px',
          align: 'center'
        },
        {
          title: '司机姓名',
          key: 'driverName',
          width: '100px',
          align: 'center'
        },
        {
          title: '装卸工',
          key: 'loaderList',
          width: '200px',
          align: 'center',
          render: (h, params) => {
            // return h('p', params.row.loaderList.map(item => {
            //   return h('p', item.realname)
            // }))
            return h('span', {
              style: {
                display: 'inline-block',
                width: '100%',
                margin: '10px',
                overflow: 'hidden',
                textOverflow: 'hidden'
              }
            }, params.row.loaderList.map(item => item.realname + ' '))
          }
        },
        {
          title: '订单详情',
          key: 'orderList',
          align: 'center',
          render: (h, params) => {
            return h('Tag', {
              style: {
                width: '80px',
                height: '20px'
              },
              nativeOn: {click: () => {
                this.$Message.info('你点击了订单详情标签')
                // this.$Message.info(params.row.orderList.map(item => item.customerName))
                console.log(params.row.orderList.map(item => item.customerName + ''))
              }}
            }, '订单详情')
          }
        },
        {
          title: '发货时间',
          key: 'deliveryTime',
          width: '150px',
          align: 'center'
        },
        {
          title: '创建时间',
          key: 'createdTime',
          width: '150px',
          align: 'center'
        },
        {
          title: '操作',
          slot: 'action',
          width: 200,
          align: 'center'
        }
      ]
    }
  },
  created () {
    this.getAllDelivery()
  },
  methods: {
    getAllDelivery () {
      let _this = this
      this.getRequest('/api/delivery/' + this.userId).then(resp => {
        console.log(resp.data)
        _this.deliveryList = resp.data
      })
    },
    showDeliveryAddModal () {
      console.log('你点击了添加发货记录按钮')
      this.addModalVisiable = true
      console.log(this.addModalVisiable)
      this.$emit('on-open-add-modal')
    },
    show (index) {
      this.$Modal.info({
        title: '详情',
        content: '内容'
      })
    },
    remove (row, index) {
      this.deliveryList.splice(index, 1)
    },
    onCloseCancelAdd (bool) {
      this.addModalVisiable = bool
    },
    onHandleAddResult (bool) {
      if (bool === true) {
        this.getAllDelivery()
      }
    },
    search () {
      this.postRequest('/api/delivery/search', this.searchParam).then(resp => {
        if (resp.status === 200) {
          this.deliveryList = resp.data
        }
      })
    }
  }
}
</script>
<style scoped>
.delivery_list {
  width: 1200px;
}
.operate {
  margin: 20px 0;
}
.btn_add {
  float: right;
}
</style>



