import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Home from '../components/Home'
import Login from '../components/Login'
import CustomerList from '../components/customer/CustomerList'
import EmployeeList from '../components/employee/EmployeeList'
import TruckList from '../components/truck/TruckList'
import OrderList from '../components/order/OrderList'
import DeliveryList from '../components/delivery/DeliveryList'
import DeliveryStatisticalAnalysis from '../components/delivery/DeliveryStatisticalAnalysis'
import CementList from '../components/cement/CementList'
import UserList from '../components/user/UserList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      name: '主页',
      component: Home,
      children: [
        {
          path: 'orderlist',
          name: '订单管理',
          component: OrderList,
          hidden: true
        },
        {
          path: 'customerlist',
          name: '客户管理',
          component: CustomerList,
          hidden: true
        },
        {
          path: 'employeelist',
          name: '员工管理',
          component: EmployeeList,
          hidden: true
        },
        {
          path: 'trucklist',
          name: '车辆管理',
          component: TruckList,
          hidden: true
        },
        {
          path: 'deliverylist',
          name: '发货记录浏览',
          component: DeliveryList,
          hidden: true
        },
        {
          path: 'deliverystatisticalanalysis',
          name: '发货记录统计分析',
          component: DeliveryStatisticalAnalysis,
          hidden: true
        },
        {
          path: 'cementlist',
          name: '水泥管理',
          component: CementList,
          hidden: true
        },
        {
          path: 'userList',
          name: '用户管理',
          component: UserList,
          hidden: true
        }
      ]
    }
  ]
})



