<template>
  <div class="customer_list">
    <div class="operate">
      用户名：
      <Input v-model="searchParam.username"  @on-change="search" style="width: 100px; margin-right: 10px" />
      姓名：
      <Input v-model="searchParam.realname"  @on-change="search" style="width: 100px; margin-right: 10px" />
      联系电话：
      <Input v-model="searchParam.phone"  @on-change="search" style="width: 100px; margin-right: 10px" />
      <Button type="primary" icon="md-search" @click="search">搜索</Button>
      <Button type="success" icon="md-add" class="btn_add" @click="showCustomerAddModal">添加用户</Button>
    </div>
    <Table border stripe :columns="userColumns" :data="userList" style="border-radius: 3px;">
      <template slot-scope="{ row, index }" slot="action">
        <Button type="primary" size="small" style="margin-right: 5px" @click="show(row, index)">编辑</Button>
        <Button type="error" size="small" @click="remove(row, index)">删除</Button>
      </template>
    </Table>
    <div v-if="addModalVisiable">
      <CustomerAddModal @on-close-cancel-add="onCloseCancelAdd" @on-handle-add-result="onHandleAddResult" ></CustomerAddModal>
    </div>
    <div v-if="modifyModalVisiable">
      <CustomerModifyModal @on-close-cancel-modify="onCloseCancelModify" @on-handle-modify-result="onHandleModifyResult" :customer="customer"></CustomerModifyModal>
    </div>
  </div>
</template>
<script>
import NormalUserAddModal from './NormalUserAddModal'

export default {
  name: 'normaluserlist',
  components: {
    NormalUserAddModal
  },
  data () {
    return {
      searchParam: {
        realname: '',
        phone: ''
      },
      userId: 2,
      addModalVisiable: false,
      user: {
        username: '',
        realname: '',
        password: '',
        phone: '',
        remark: ''
      },
      userList: [],
      userColumns: [
        {
          title: '用户编号',
          key: 'id',
          width: '100px',
          align: 'center'
        },
        {
          title: '用户名',
          key: 'username',
          width: '100px',
          align: 'center'
        },
        {
          title: '姓名',
          key: 'realname',
          width: '150px',
          align: 'center'
        },
        {
          title: '联系电话',
          key: 'phone',
          width: '150px',
          align: 'center'
        },
        {
          title: '备注',
          key: 'remark',
          align: 'center'
        },
        {
          title: '操作',
          slot: 'action',
          width: 200,
          align: 'center'
        }
      ]
    }
  },
  created () {
    this.getUser()
  },
  methods: {
    search () {
      console.log(this.searchParam)
      this.postRequest('/api/user/search', this.searchParam).then(resp => {
        if (resp.status === 200) {
          this.customerList = resp.data
        }
      })
    },
    getUser () {
      let _this = this
      this.getRequest('/api/admin/').then(resp => {
        console.log(resp.data)
        _this.userList = resp.data
      })
    },
    show (row, index) {
      this.customer = row
      this.modifyModalVisiable = true
      this.$emit('on-open-modify-modal')
    },
    onCloseCancelModify (bool) {
      this.modifyModalVisiable = bool
    },
    onHandleModifyResult (bool) {
      if (bool === true) {
        this.getAllCustomer()
      }
    },
    remove (row, index) {
      console.log(row.id)
      this.deleteRequest('/api/user/delete/' + row.id).then(resp => {
        console.log(resp)
        if (resp.status === 200) {
          this.$Message.info('删除成功')
          this.customerList.splice(index, 1)
        }
      })
    },
    showCustomerAddModal () {
      this.addModalVisiable = true
      this.$emit('on-open-add-modal')
    },
    onCloseCancelAdd (bool) {
      this.addModalVisiable = bool
    },
    onHandleAddResult (bool) {
      if (bool === true) {
        this.getAllCustomer()
      }
    }
  }
}
</script>
<style scoped>
.customer_list {
  width: 900px;
}
.operate {
  margin: 20px 0;
}
.btn_add {
  float: right;
}
</style>



