<template>
  <div>
    <!-- 添加车辆表单 -->
    <Modal v-model="modal" title="添加车辆"
    @on-ok="addTruck"
    @on-cancel="onCancel"
    :on-open-add-modal="modal=true"
    :truck="truck">
      <Form ref="setTruck" :model="truck" :rules="truckValidate" :label-width="120">
        <FormItem label="车牌号" prop="licensePlate" >
          <Input v-model="truck.licensePlate" placeholder="请输入车牌号" style="width: 220px;"></Input>
        </FormItem>
        <FormItem label="最大装载容量" prop="maxCapacity">
          <InputNumber :max="100" :min="1" :step="1" v-model="truck.maxCapacity"></InputNumber>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
<script>
export default {
  name: 'truckadd',
  data () {
    return {
      modal: false,
      truck: {
        licensePlate: '',
        maxCapacity: null,
        userId: 2
      },
      truckValidate: {
        licensePlate: [
          {
            required: true,
            message: '车牌号不能为空'
          }
        ],
        maxCapacity: [
          {
            required: true,
            message: '最大装载容量不能为空'
          }
        ]
      }
    }
  },
  methods: {
    onCancel () {
      this.$emit('on-close-cancel-add', false)
    },
    addTruck: function () {
      // this.$refs.setTruck.validate(async (valid) => {
      //   if (valid) {
      //     console.log(this.truck)
      //     this.postRequest('/api/truck/add', this.truck).then(resp => {
      //       console.log(resp)
      //       if (resp.status === 200) {
      //         this.$Message.info('添加成功')
      //         this.$emit('on-handle-add-result', true)
      //         this.loading = false
      //       } else {
      //         this.$Message.info(resp.msg)
      //       }
      //     })
      //     // let  res = await this.$ajax.post('/xx/xx',{})
      //     // if(res.cd == 0){
      //     //   //doSomething..
      //     // }else{
      //     //   this.$Message.info(res.msg)
      //     // }
      //   } else {
      //     // 对话框校验失败，取消loading状态
      //     this.loading = false
      //     setTimeout(() => {
      //       this.$nextTick(() => {
      //         this.loading = true
      //       })
      //     }, 100)
      //   }
      // })
      console.log(this.truck)
      this.postRequest('/api/truck/add', this.truck).then(resp => {
        console.log(resp)
        if (resp.status === 200) {
          this.$Message.info('添加成功')
          this.$emit('on-close-cancel-add', false)
          this.$emit('on-handle-add-result', true)
        }
      })
    }
  }
}
</script>



