<template>
  <div>
    <!-- 添加、修改客户表单 -->
    <Modal  title="添加客户" @on-ok="addCustomer"
    @on-cancel="onCancel"
    :on-open-add-modal="modal=true" :customer="customer" v-model="modal">
      <Form ref="setCustomer" :model="customer" :rules="customerValidate" :label-width="120">
        <FormItem label="姓名" prop="realname" >
          <Input v-model="customer.realname" placeholder="请输入姓名" style="width: 220px;"></Input>
        </FormItem>
        <FormItem label="联系电话" prop="phone">
          <Input v-model="customer.phone" placeholder="请输入联系电话" style="width: 220px;"></Input>
        </FormItem>
        <FormItem label="备注" prop="remark">
          <Input v-model="customer.remark" style="width: 220px;"></Input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
<script>
export default {
  name: 'cutomeradd',
  // props: ['customer'],
  data () {
    return {
      modal: false,
      customer: {
        realname: '',
        phone: '',
        remark: '',
        ownerId: 2
      },
      customerValidate: {
        realname: [
          {
            required: true,
            message: '姓名不能为空'
          }
        ],
        phone: [
          {
            required: true,
            message: '联系电话不能为空'
          }
        ]
      }
    }
  },
  methods: {
    onCancel () {
      // this.$refs['setCustomer'].resetFields();
      this.$emit('on-close-cancel-add', false)
    },
    addCustomer () {
      console.log('添加客户')
      console.log(this.cusotmer)
      this.postRequest('/api/user/addCustomer', this.customer).then(resp => {
        console.log(resp)
        if (resp.status === 200) {
          this.$Message.info('客户添加成功')
          this.$emit('on-close-cancel-add', false)
          this.$emit('on-handle-add-result', true)
        }
      })
    }
  }

}
</script>



