<template>
  <div>
    <!-- 添加发货记录表单 -->
    <Modal v-model="modal" title="添加发货记录"
    @on-ok="addDelivery"
    @on-cancel="onCancel"
    :on-open-add-modal="modal=true">
      <Form ref="" :model="delivery" :rules="deliveryValidate" :label-width="120">
        <FormItem label="车辆" prop="truckId" >
          <Select v-model="delivery.truckId" style="width:150px" filterable>
            <Option v-for="item in truckList" :value="item.id" :key="item.id">{{ item.licensePlate }}</Option>
          </Select>
        </FormItem>
        <FormItem label="司机" prop="driverId">
          <Select v-model="delivery.driverId" style="width:150px" filterable>
            <Option v-for="item in driverList" :value="item.id" :key="item.id">{{ item.realname }}</Option>
          </Select>
        </FormItem>
        <FormItem label="订单" prop="orderIds">
          <Select v-model="delivery.orderIds" style="width:150px"  filterable multiple>
            <Option v-for="item in orderList" :value="item.id" :key="item.id">{{ item.id }}
              <!-- <Tooltip placement="right">
                文字提示
                <template slot-scope="scope">
              <div slot="content">
                <template>
                  <div>
                    <Tag>客户姓名</Tag>
                    {{scope.row.item.customerName}}
                  </div>
                  <div>
                    <Tag>收货地址</Tag>
                    {{scope.row.item.customerName}}
                  </div>
                  <div>
                    <Tag>水泥名称</Tag>
                    {{scope.row.item.cementName}}
                  </div>
                  <div>
                    <Tag>单价</Tag>
                    {{scope.row.item.unitPrice}}
                  </div>
                  <div>
                    <Tag>数量</Tag>
                    {{scope.row.item.num}}
                  </div>
                  <div>
                    <Tag>创建时间</Tag>
                    {{scope.row.item.createdTime}}
                  </div>
                </template>
              </div>
            </template>
              </Tooltip> -->
            </Option>
          </Select>
        </FormItem>
        <FormItem label="装卸工" prop="loaderIds">
          <Select v-model="delivery.loaderIds" style="width:150px" filterable multiple>
            <Option v-for="item in loaderList" :value="item.id" :key="item.id">{{ item.realname }}</Option>
          </Select>
        </FormItem>
        <FormItem label="发货时间" prop="deliveryTime">
          <DatePicker v-model="delivery.deliveryTime" type="datetime" format="yyyy-MM-dd HH:mm" placeholder="Select date and time(Excluding seconds)" style="width: 200px"></DatePicker>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
<script>
export default {
  name: 'deliveryadd',
  data () {
    return {
      modal: false,
      userId: 2,
      delivery: {
        truckId: null,
        driverId: null,
        deliveryTime: new Date(),
        userId: 2,
        orderIds: [],
        loaderIds: []
      },
      truckList: [],
      driverList: [],
      orderList: [],
      loaderList: [],
      deliveryValidate: {
        truckId: [
          {
            required: true,
            message: '车辆不能为空'
          }
        ],
        driverId: [
          {
            required: true,
            message: '司机不能为空'
          }
        ],
        deliveryTime: [
          {
            required: true,
            message: '发货时间不能为空'
          }
        ],
        orderIds: [
          {
            required: true,
            message: '订单不能为空'
          }
        ],
        loaderIds: [
          {
            required: true,
            message: '装卸工不能为空'
          }
        ]
      }
    }
  },
  created () {
    this.getTruck()
    this.getDriver()
    this.getUncompletedOrders()
    this.getLoaders()
  },
  methods: {
    getTruck () {
      let _this = this
      this.getRequest('/api/truck/user/' + this.userId).then(resp => {
        if (resp.status === 200) {
          _this.truckList = resp.data
        }
      })
    },
    getDriver () {
      let _this = this
      this.getRequest('/api/user/driver/' + this.userId).then(resp => {
        if (resp.status === 200) {
          _this.driverList = resp.data
        }
      })
    },
    getUncompletedOrders () {
      let _this = this
      this.getRequest('/api/orders/uncompleted/' + this.userId).then(resp => {
        if (resp.status === 200) {
          _this.orderList = resp.data.data
        }
      })
    },
    getLoaders () {
      let _this = this
      this.getRequest('/api/user/loader/' + this.userId).then(resp => {
        if (resp.status === 200) {
          _this.loaderList = resp.data
        }
      })
    },
    addDelivery () {
      console.log(this.delivery)
      this.postRequest('/api/delivery/add', this.delivery).then(resp => {
        console.log(resp)
        if (resp.status === 200) {
          this.$Message.info('发货记录添加成功')
          this.$emit('on-handle-add-result', true)
        }
      })
    },
    onCancel () {
      this.$emit('on-close-cancel-add', false)
    }
  }
}
</script>



