</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      user: {
        username: '',
        password: ''
      }
      // userValidate: {
      //   username: [
      //     {
      //       required: true,
      //       message: '用户名不能为空'
      //     }
      //   ],
      //   password: [
      //     {
      //       required: true,
      //       message: '密码不能为空'
      //     }
      //   ]
      // }
    }
  },
  methods: {
    login () {
      this.$router.push('/home')
    }
  }
}
</script>

